html,
body {
  height: 100%;
  margin: 0;
}

@keyframes flicker {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
  }
}

@keyframes blinkCursor {
  50% {
    border-right-color: transparent;
  }
}

@keyframes typeAndDelete {
  0%,
  10% {
    width: 0;
  }
  45%,
  55% {
    width: 24rem;
  } /* adjust width based on content */
  90%,
  100% {
    width: 0;
  }
}

.weather {
  margin-top: 8vh;
  margin-bottom: 6vh;
  min-width: 400px;
}

.weather-login {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.text {
  white-space: nowrap;
  overflow: hidden;
  /* border-right: 0.1em solid; */
  animation: typeAndDelete 5s steps(11) infinite,
    blinkCursor 0.5s step-end infinite alternate;
  margin-top: 10px;
  animation-timing-function: linear;
}

.text-login {
  text-align: center;
}
